@font-face {
  font-family: Montserrat-Regular;
  src: url("../fonts/montserrat/Montserrat-Regular.ttf");
  font-weight: normal; }

@font-face {
  font-family: Montserrat-Light;
  src: url("../fonts/montserrat/Montserrat-Light.ttf"); }

@font-face {
  font-family: Montserrat-Bold;
  src: url("../fonts/montserrat/Montserrat-Bold.ttf");
  font-weight: bold; }

.wrapper-top {
  display: flex;
  width: 100%;
  flex-direction: column;
  justify-content: center;
  height: 880px;
  background: url("../images/bg-header.jpg") no-repeat; }

.header {
  height: 115px;
  border-bottom: 1px solid #838383; }
  .header .container {
    justify-content: space-between;
    align-items: center;
    height: 100%; }

.nav {
  display: flex;
  align-items: center;
  height: 100%; }
  .nav-list {
    display: flex;
    align-items: center; }
  .nav-link {
    font-size: 17px;
    font-size: 1.0625rem;
    padding: 10px 24px;
    color: #fff;
    text-transform: capitalize;
    transition: 0.3s; }
    .nav-link:hover {
      color: #11749e; }

.social {
  display: flex; }
  .social-link {
    padding: 15px;
    color: #fff;
    transition: 0.3s; }
    .social-link:hover {
      color: #11749e; }

.banner {
  display: flex;
  justify-content: center;
  flex-direction: column;
  align-items: center;
  height: 100%;
  text-transform: uppercase; }
  .banner-text {
    color: #fff;
    margin: 0;
    font-size: 150px;
    font-size: 9.375rem; }
  .banner-text__sub {
    color: #11749e;
    margin: 0;
    font-size: 20px;
    font-size: 1.25rem;
    letter-spacing: 20px; }
  .banner-scroll {
    width: 75px;
    height: 80px;
    margin: 15px auto;
    border-radius: 50%;
    color: #fff;
    cursor: pointer;
    background-color: transparent;
    border: 2px solid #fff;
    transition: 0.3s;
    outline: none; }
    .banner-scroll:hover {
      border: 2px solid #11749e;
      color: #11749e;
      transition: translateY 0.3s; }
      .banner-scroll:hover .banner-scroll__arrow {
        transition-duration: 0.5s;
        transform: translateY(5px); }

.feature, .blog, .team, .form {
  height: 740px;
  background-color: #11749e;
  color: #fff; }
  .feature .container, .blog .container, .team .container, .form .container {
    display: flex;
    flex-direction: column; }
  .feature-list {
    display: flex;
    justify-content: space-between; }
  .feature-card {
    display: flex;
    flex-direction: column;
    text-align: center;
    max-width: 270px;
    justify-content: center; }
  .feature-icon, .blog-img {
    display: flex;
    align-self: center;
    align-items: center;
    justify-content: center;
    border: 2px solid #2db0e8;
    border-radius: 50px;
    width: 100px;
    height: 100px; }
  .feature-heading, .blog-heading {
    font-size: 18px;
    font-size: 1.125rem;
    text-transform: uppercase; }
  .feature-text {
    line-height: 22px; }

.section-heading, .team .team-section__heading {
  position: relative;
  display: flex;
  justify-content: center;
  padding-top: 170px;
  padding-bottom: 100px;
  font-size: 37px;
  font-size: 2.3125rem;
  z-index: 2; }
  .section-heading:before, .team .team-section__heading:before {
    content: 'W';
    position: absolute;
    left: 50%;
    top: 50%;
    transform: translate(-50%, -50%);
    font-size: 175px;
    font-size: 10.9375rem;
    z-index: 1;
    color: rgba(255, 255, 255, 0.1); }

.posts {
  display: flex;
  flex-direction: column; }

.post {
  display: flex;
  background-color: #222;
  color: #fff; }
  .post-date {
    width: 450px;
    font-size: 13px;
    font-size: 0.8125rem;
    margin: 15px 0; }
  .post-left {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    width: 50%; }
  .post-right {
    width: 50%; }
  .post-img {
    width: 100%;
    height: 100%; }
  .post:nth-child(even) {
    flex-flow: row-reverse;
    align-items: flex-start;
    background-color: #f3f3f3;
    color: #222; }
    .post:nth-child(even) .post-left {
      align-items: flex-start; }
    .post:nth-child(even) .post-date {
      margin: 15px 55px; }
    .post:nth-child(even) .post-heading {
      margin: 10px 55px; }
  .post-heading {
    width: 450px;
    text-align: left;
    font-size: 20px;
    margin: 10px 0;
    text-transform: uppercase; }
  .post-text {
    width: 450px;
    margin: 0 25px;
    line-height: 25px;
    letter-spacing: 1.1px; }

.blog {
  background-color: #fff;
  color: #222;
  height: auto; }
  .blog-section__heading {
    text-transform: uppercase; }
    .blog-section__heading:before {
      content: 'B';
      position: absolute;
      left: 50%;
      top: 50%;
      transform: translate(-50%, -50%);
      font-size: 175px;
      font-size: 10.9375rem;
      z-index: 1;
      color: #222;
      opacity: 0.15; }
  .blog-list {
    display: flex;
    flex-flow: row wrap;
    justify-content: space-around; }
  .blog-card {
    display: flex;
    flex-direction: column;
    text-align: center;
    margin: 0 15px 10px 0;
    max-width: 270px;
    justify-content: center; }
  .blog-img {
    width: 100%;
    height: 280px;
    border: none;
    border-radius: inherit; }
  .blog-heading {
    color: #11749e;
    margin: 30px 0 0; }

.quotes {
  display: flex;
  min-height: 355px;
  background-color: #11749e;
  color: #fff; }
  .quotes .container {
    align-self: center; }
  .quotes-block {
    display: flex;
    align-items: center;
    width: 50%; }
  .quotes-icon {
    margin: 0 20px; }

.team {
  background-color: #fff;
  color: #222;
  height: auto; }
  .team .team-section__heading {
    margin-bottom: 0;
    text-transform: uppercase; }
    .team .team-section__heading:before {
      content: 'T';
      color: #222;
      opacity: 0.15; }
  .team-subtitle {
    font-size: 25px;
    font-size: 1.5625rem;
    text-align: center; }
  .team-list {
    display: flex;
    justify-content: space-around; }
  .team-card {
    position: relative;
    width: 270px;
    height: 415px; }
  .team-img {
    width: 100%;
    height: 100%; }
  .team-text {
    width: 80%;
    background-color: #fff;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    text-align: center;
    margin-top: -30px;
    padding: 10px 50px;
    text-transform: uppercase; }
  .team-name {
    font-size: 15px;
    font-size: 0.9375rem;
    margin: 0;
    padding: 0;
    display: inline-block;
    color: #11749e; }
  .team-profession {
    display: block;
    font-size: 14px;
    font-size: 0.875rem;
    margin: 10px 0; }

.form {
  margin-top: 50px;
  background: url("../images/bg-form.jpg") no-repeat;
  background-size: 100%; }
  .form-section__heading {
    text-transform: uppercase; }
    .form-section__heading:before {
      content: 'C'; }
  .form-block {
    display: flex;
    flex-flow: column wrap;
    justify-content: space-around; }
  .form-inputs {
    display: flex;
    justify-content: space-between;
    margin-bottom: 40px; }
  .form-input, .form-textarea {
    flex-basis: 370px;
    padding: 22px 20px;
    background-color: transparent;
    border: 1px solid #fff;
    color: #fff; }
    .form-input::placeholder, .form-textarea::placeholder {
      color: #fff; }
  .form-textarea {
    width: 100%;
    height: 150px;
    resize: none; }
  .form-button {
    align-self: center;
    margin-top: 40px;
    width: 220px;
    height: 60px;
    border: none;
    background-color: #11749e;
    color: #fff;
    text-transform: uppercase;
    font-size: 24px;
    font-size: 1.5rem;
    font-weight: bold;
    letter-spacing: 1.2px; }
